html {
  font-size: 62.5%;
  background-color: #fefefe; }

body {
  font-size: 1.6rem;
  color: #454545;
  font-family: 'Open Sans', 'Arial';
  background-color: #aaaaaa; }

* {
  font-size: inherit;
  font-weight: normal;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  color: inherit;
  line-height: inherit; }

ul {
  padding-left: 2rem; }

.heading-primary {
  font-size: 4rem;
  margin: 0 0 3rem 0;
  position: relative;
  font-weight: lighter; }
  .heading-primary::after {
    content: ' ';
    width: 30rem;
    height: 1px;
    background-color: #454545;
    position: absolute;
    bottom: -10px;
    left: 0; }

/* PrismJS 1.17.1
https://prismjs.com/download.html#themes=prism-tomorrow&languages=markup+css+clike+javascript */
/**
 * prism.js tomorrow night eighties for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/chriskempson/tomorrow-theme
 * @author Rose Pritchard
 */
code[class*="language-"],
pre[class*="language-"] {
  color: #333;
  background: none;
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  font-size: 1em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none; }

/* Code blocks */
pre[class*="language-"] {
  background-color: #f6f6f6;
  border: 1px solid #dadada;
  border-radius: 3px;
  padding: 2px 5px;
  font-size: 1.46rem;
  overflow-x: auto; }

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #f6f6f6; }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal; }

.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #3cbb58;
  font-style: italic; }

.token.punctuation {
  color: #333; }

.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
  color: #e01e5a; }

.token.function-name {
  color: #6196cc; }

.token.boolean,
.token.number,
.token.function {
  color: #f08d49; }

.token.property,
.token.class-name,
.token.constant,
.token.symbol {
  color: #d29207; }

.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
  color: #c42cd6; }

.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
  color: #179215; }

.token.operator,
.token.entity,
.token.url {
  color: #67cdcc; }

.token.important,
.token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.entity {
  cursor: help; }

.token.inserted {
  color: green; }

.header {
  position: relative;
  color: #fff;
  text-align: center;
  padding: 5rem; }
  .header__image {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-position: center;
    background-size: cover;
    background-image: url("../img/header.jpg");
    z-index: 0; }
  .header__bulb {
    width: 10rem;
    position: relative; }
  .header__slider {
    width: 500px;
    max-width: 100%;
    margin: 0 auto; }
  .header__slide-title {
    font-size: 4rem; }
  .header__title {
    font-size: 6rem;
    position: relative; }
  .header__title-span {
    margin-top: 2rem;
    font-size: 3rem;
    display: block;
    text-shadow: 1px 1px 0 #000;
    position: relative; }

.main {
  width: calc(100% - 4rem);
  max-width: 80rem;
  margin: 0 auto;
  padding-bottom: 2rem; }

.main-section {
  background-color: #fff;
  margin: 2rem 0;
  padding: 2rem;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2); }
  .main-section:last-of-type {
    margin-bottom: 0; }

.article {
  text-align: justify;
  line-height: 1.6; }
  .article:not(:last-child) {
    margin-bottom: 2rem; }
  .article__title {
    font-size: 2.4rem;
    margin: 0 0 1rem 0;
    line-height: 1.3;
    text-align: left; }
  .article p:not(:last-child) {
    margin-bottom: 1rem; }

.nav {
  background-color: #444;
  color: #fff; }
  .nav__list {
    width: 100%;
    max-width: 80rem;
    margin: 0 auto;
    display: flex;
    background-color: #1e1e1e;
    padding: 0; }
  .nav__item {
    padding: 1rem;
    display: block;
    transition: background-color 200ms ease-out; }
    .nav__item:hover {
      background-color: #666; }
  .nav__link {
    font-size: 2rem; }

.callback-checkbox {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #dedede; }
  .callback-checkbox__container {
    flex-grow: 1;
    border: 1px solid #dedede; }
  .callback-checkbox__label {
    padding: 10px;
    display: inline-block; }

.callback-slider {
  margin: 2rem 0; }
  .callback-slider__slide {
    text-align: center;
    padding: 2rem;
    border: 1px solid #888; }

#callback-log {
  box-sizing: border-box;
  width: 100%;
  height: 150px;
  resize: vertical;
  padding: 10px; }

.default-slider {
  margin: 2rem 0; }
  .default-slider__slide {
    text-align: center;
    padding: 2rem;
    border: 1px solid #888; }

.loop-slider {
  margin-left: -1rem;
  width: calc(100% + 2rem); }
  .loop-slider__slide {
    text-align: center;
    padding: 2rem;
    border: 1px solid #888;
    margin: 1rem; }

.responsive-slider {
  margin-left: -1rem;
  width: calc(100% + 2rem); }
  .responsive-slider__slide {
    text-align: center;
    padding: 2rem;
    border: 1px solid #888;
    margin: 1rem; }

.table {
  width: 100%;
  border-collapse: collapse; }
  .table__td {
    border: 1px solid #dedede;
    vertical-align: top;
    font-family: 'consolas';
    padding: 2px 5px;
    text-align: left;
    line-height: 2rem; }

.thumbnail-slider {
  width: calc(100% + 2rem);
  margin-left: -1rem; }
  .thumbnail-slider--large {
    height: 20rem;
    margin-bottom: 2rem; }
  .thumbnail-slider--small {
    height: 10rem; }
  .thumbnail-slider--small .thumbnail-slider__slide {
    opacity: 0.7; }
    .thumbnail-slider--small .thumbnail-slider__slide--active {
      opacity: 1; }
  .thumbnail-slider__slide {
    height: 100%;
    margin: 0 1rem; }
  .thumbnail-slider__content {
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }
